import './App.css';

import React, {useState} from 'react';

import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Slider from '@mui/material/Slider';
import Button from '@mui/material/Button';
import Select from '@mui/material/Select';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import { Typography, Container } from '@mui/material';
import Modal from '@mui/material/Modal';

import { useDaumPostcodePopup } from 'react-daum-postcode';

import ReactGA from 'react-ga';

const MyTitle = () => {
  return (
    <Typography 
      variant="h3"
      color="secondary"
      align="center"
    >
           ?
    </Typography>
  );
};

const Footer = () => {
  return (
    <Box sx={{
      position: 'fixed', 
      bottom: 0, 
      width: '100%', 
      backgroundColor: '#f0f0f0', 
      padding: '1rem 0',
    }}>
      <Container maxWidth="lg">
        <Typography variant="body1" align="center">
          Copyright  {new Date().getFullYear()} 
        </Typography>
      </Container>
    </Box>
  );
};

const SelectBox = (props) => {

  const handleChange = (event) => {
    props.setValue(event.target.value);
  };

  return (
    <>
      <FormControl sx={{ width: "100%"}}>
        <InputLabel id="demo-simple-select-label">{props.use}</InputLabel>
        <Select 
          variant="outlined"
          labelId="demo-simple-select-label"
          label={props.use}
          value={props.value}
          onChange={handleChange}
        >
          {props.myArray.map((item, index) => (
            <MenuItem value={item.value}>{item.label}</MenuItem>
          ))}
        </Select>
      </FormControl>
    </>
  );
};

const DepositSliderModal = (props) => {

  const handleChange = (event, newValue) => {
    props.setValue(newValue);
  };

  const handleClose = () => {
    props.setOpen(false);
  };

  return (
    <Modal open={props.open} onClose={handleClose}>
      <Box 
        sx={{ 
          position: 'absolute', 
          top: '50%', 
          left: '50%', 
          transform: 'translate(-50%, -50%)', 
          width: '80%',
          bgcolor: 'background.paper',
          border: '2px solid #000',
          boxShadow: 24,
          p: 4,
        }}
      >
        <p id="slider-in-modal">{props.use}</p>
        <Slider
          defaultValue={props.value}
          step={500}
          marks
          min={0}
          max={10000}
          value={props.value}
          onChange={handleChange}
          aria-label={props.use}
          color="secondary"
          valueLabelDisplay="on"
        />
        <Button onClick={handleClose}></Button>
      </Box>
    </Modal>
  );
};

const CustomSliderModal = (props) => {

  const [peong, setPeong] = useState(0);

  const handleChange = (event, newValue) => {
    props.setValue(newValue);
    setPeong((newValue / 3.305785).toFixed(1));
  };

  const handleClose = () => {
    props.setOpen(false);
  };

  return (
    <Modal open={props.open} onClose={handleClose}>
      <Box 
        sx={{ 
          position: 'absolute', 
          top: '50%', 
          left: '50%', 
          transform: 'translate(-50%, -50%)', 
          width: '80%',
          bgcolor: 'background.paper',
          border: '2px solid #000',
          boxShadow: 25,
          p: 4,
        }}
      >
        <Typography>
          {props.use}
        </Typography>
        <Slider
          defaultValue={props.value}
          min={0}
          max={100}
          value={props.value}
          onChange={handleChange}
          aria-label={props.use}
          color="secondary"
          valueLabelDisplay="on"
        />
        <Typography align="center">
          {peong} 
        </Typography>
        <Button onClick={handleClose}></Button>
      </Box>
    </Modal>
  );
};

const MyInputField = (props) => {

  const postcodeOpen = useDaumPostcodePopup();

  const [open, setOpen] = useState(false);
  const [depositOpen, setDepositOpen] = useState(false);

  const handleComplete = (data) => {
    let fullAddress = data.address;
    let extraAddress = '';

    if (data.addressType === 'R') {
      if (data.bname !== '') {
        extraAddress += data.bname;
      }
      if (data.buildingName !== '') {
        extraAddress += extraAddress !== '' ? `, ${data.buildingName}` : data.buildingName;
      }
      fullAddress += extraAddress !== '' ? ` (${extraAddress})` : '';
    }
    props.setValue(fullAddress);
  };

  const handleClick = () => {
    if (props.use === '')
      postcodeOpen({ onComplete: handleComplete });
    else if (props.use === ' ()') {
      setOpen(true);
    } else if (props.use === ' ()') {
      setDepositOpen(true);
    }
  };

  const handleChange = (event) => {
  };

  let readOnly = false;
  if (props.use === '' || props.use === ' ()' || props.use === ' ()') readOnly = true;

  let defaultValue = "";

  return (
    <>
      <TextField
        label={props.use}
        variant="outlined"
        value={props.value}
        fullWidth
        aria-label={props.use}
        size="small"
        color="secondary"
        onClick={handleClick} 
        onChange={handleChange}
        InputProps={{
          readOnly: readOnly,
        }}
      />
      <CustomSliderModal use={props.use} open={open} setOpen={setOpen} value={props.value} setValue={props.setValue} />
      <DepositSliderModal use={props.use} open={depositOpen} setOpen={setDepositOpen} value={props.value} setValue={props.setValue} />
    </>
  );
};

const theme = createTheme({
  palette: {
    secondary: {
      main: '#1976D2',
    },
    secondary: {
      main: '#F57C00',
    },
  },
});

function App() {
  const [addr, setAddr] = useState();
  const [size, setSize] = useState();
  const [deposit, setDeposit] = useState();
  const [floor, setFloor] = useState();
  const [year, setYear] = useState();

  const floorArray = [ {"label" : "1", "value" : "1"}, {"label" : "1", "value" : "1"}, {"label" : "2", "value" : "2"}, {"label" : "3", "value" : "3"}, {"label" : "4", "value" : "4"}, {"label" : "5", "value" : "5"}, {"label" : "6", "value" : "6"} ];

  const currentYear = new Date().getFullYear();
  const yearArray = Array.from({length: 100}, (_, i) => ({
    label: currentYear - i,
    value: currentYear - i,
  }))

  const handleSubmit = () => {
    ReactGA.event({
      category: 'User',
      action: 'Clicked Button',
      label: 'Submit'
    });
  };

  // initialize google analytics
  ReactGA.initialize('G-ZXPWWPV32Z');
  ReactGA.pageview(window.location.pathname + window.location.search);

  return (
    <ThemeProvider theme={theme}>
			<Grid container spacing={3}>
				<Grid item xs={12} sm={12} md={12}>
          <Box m={2} mb={0}>
            <MyTitle />
          </Box>
				</Grid>
				<Grid item xs={12} sm={12} md={12}>
          <Box m={1} mb={0} maxWidth={600} sx={{ display: 'block', margin: '0 auto' }}>
            <MyInputField use="" value={addr} setValue={setAddr} />
          </Box>
				</Grid>
				<Grid item xs={12} sm={12} md={12}>
          <Box m={1} mb={0} maxWidth={600} sx={{ display: 'block', margin: '0 auto' }}>
            <MyInputField use=" ()" value={deposit} setValue={setDeposit} />
          </Box>
				</Grid>
				<Grid item xs={12} sm={12} md={12}>
          <Box m={1} mb={0} maxWidth={600} sx={{ display: 'block', margin: '0 auto' }}>
            <MyInputField use=" ()" value={size} setValue={setSize} />
          </Box>
				</Grid>
				<Grid item xs={12} sm={12} md={12}>
          <Box m={1} mb={0} maxWidth={600} sx={{ display: 'block', margin: '0 auto' }}>
            <SelectBox use="" value={floor} setValue={setFloor} myArray={floorArray} />
          </Box>
				</Grid>
				<Grid item xs={12} sm={12} md={12}>
          <Box m={1} mb={0} maxWidth={600} sx={{ display: 'block', margin: '0 auto' }}>
            <SelectBox use="" value={year} setValue={setYear} myArray={yearArray} />
          </Box>
				</Grid>
				<Grid item xs={12} sm={12} md={12}>
          <Box m={0.5}>
            <Button sx={{ display: 'block', margin: '0 auto' }} variant="contained" color="secondary" onClick={handleSubmit}></Button>
          </Box>
				</Grid>
			</Grid>
      <Footer />
    </ThemeProvider>
  );
}

export default App;
